@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
:root{--darkGray: #2b2b2b;--lightGray: #969696}*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Rubik", sans-serif;font-size:18px}

.app{background-image:url(/ip-tracker/static/media/pattern-bg.a60a3c85.png);background-repeat:no-repeat;background-size:100% 300px;overflow:auto;height:100vh;position:relative}.app__title{color:#fff;text-align:center;font-weight:500;font-size:26px;line-height:30px;letter-spacing:-0.232143px;margin-top:26px;margin-bottom:29px}@media (min-width: 992px){.app__title{font-size:32px;margin-top:33px;margin-bottom:31px}}

.search__form{display:flex;width:327px;height:58px;margin:0 auto;margin-bottom:24px;box-shadow:0px 50px 50px -25px rgba(0,0,0,0.0983665)}.search__input{height:100%;width:269px;border:none;border-radius:15px 0px 0px 15px;outline:none;padding-left:24px;font-style:normal;font-weight:normal;font-size:18px;line-height:21px;color:var(--darkGray)}.search__btn{height:100%;width:58px;border:none;background:url(/ip-tracker/static/media/icon-arrow.2eaaa5ee.svg),#000;background-repeat:no-repeat;background-position:center;border-radius:0px 15px 15px 0px;outline:none}@media (min-width: 992px){.search__form{width:555px;margin-bottom:48px}.search__input{width:497px}.search__input:hover{cursor:pointer}.search__btn:hover{cursor:pointer;background-color:#3f3f3f}}

.details{width:327px;margin:0 auto;background:#ffffff;box-shadow:0px 50px 50px -25px rgba(0,0,0,0.0983665);border-radius:15px;padding:24px;position:relative;z-index:1}.detail{text-align:center;margin-bottom:24px}.detail:last-child{margin-bottom:0}.detail__name{font-style:normal;font-weight:bold;font-size:10px;line-height:12px;letter-spacing:1.45833px;color:var(--lightGray);margin-bottom:7px}.detail__data{font-style:normal;font-weight:500;font-size:20px;line-height:24px;letter-spacing:-0.178571px;color:var(--darkGray)}@media (min-width: 992px){.details{display:flex;align-items:center;width:1110px;height:161px}.detail{min-width:213px;padding:10px;margin-bottom:0;margin-left:32px;border-right:1px solid #979797;text-align:left}.detail:first-child{margin-left:0}.detail:last-child{border:none}.detail__name{margin-bottom:13px}.detail__data{font-size:26px}}

.spinner{display:inline-block;width:25px;height:25px;position:relative;margin:0 auto}.spinner span{position:absolute;display:inline-block;box-sizing:border-box;width:100%;height:100%;border:5px solid transparent;border-top-color:#ad60f5;border-radius:50%;animation:spinner 1.2s linear infinite}.spinner span:nth-child(2){border:5px solid transparent;border-bottom-color:#ad60f5}@keyframes spinner{0%{transform:rotate(0deg);border-width:5px}50%{transform:rotate(180deg);border-width:1px}100%{transform:rotate(360deg);border-width:5px}}

.map{position:absolute;width:100%;top:300px;height:calc(100vh - 300px);z-index:0;background-color:cornflowerblue}

